"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { Plus } from "lucide-react"
import { Button } from "@/components/ui/button"
import { ContractsTable } from "@/src/features/contracts/components/contracts-table"
import { useSupabase } from "@/src/components/providers/supabase-provider"
import { createApiClient } from "@/src/lib/api/client"
import { Contract } from "@/src/types"
import { Dialog, DialogContent, DialogTrigger, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { ContractForm } from "@/src/features/contracts/components/contract-form"

export default function ContractsPage() {
  const { session } = useSupabase()
  const [contracts, setContracts] = useState<Contract[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    if (!session) return

    const fetchContracts = async () => {
      try {
        const api = createApiClient(session)
        const res = await api.contracts.list()
        if ((res as any).success) {
          setContracts((res as any).data)
        } else {
          console.error("API returned failure:", res)
          setContracts([])
        }
      } catch (error) {
        console.error("Failed to fetch contracts", error)
      } finally {
        setIsLoading(false)
      }
    }

    fetchContracts()
  }, [session])

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="font-heading text-2xl font-bold text-[#0A2540]">Contracts</h1>
          <p className="text-muted-foreground">Manage service contracts and schedules</p>
        </div>
           <Dialog>
             <DialogTrigger asChild>
                <Button className="bg-[#00C49A] hover:bg-[#00A07D] text-white">
                  <Plus className="mr-2 h-4 w-4" />
                  New Contract
                </Button>
             </DialogTrigger>
             <DialogContent className="w-[95vw] max-w-md sm:max-w-xl max-h-[90vh] overflow-y-auto">
               <DialogHeader>
                  <DialogTitle>Create New Contract</DialogTitle>
               </DialogHeader>
               <ContractForm 
                  onSuccess={() => {
                    // Refresh
                    // Since page uses effective local state via useEffect, likely need to reload or re-fetch.
                    // The simplest is re-fetch or reload window if router.refresh() doesn't work deep enough.
                    window.location.reload() 
                  }}
               />
             </DialogContent>
           </Dialog>
      </div>

      <ContractsTable data={contracts} isLoading={isLoading} />
    </div>
  )
}
